  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'gilroyregular';
  src: url("../fonts/gilroy-regular-webfont.woff2") format("woff2"), url("../fonts/gilroy-regular-webfont.woff") format("woff");
