  margin: 0;
  font-weight: 400; }

h2 {
  font-size: 36px;
  line-height: 42px; }

h2 {
  font-size: 24px;
  line-height: 28px;
  font-weight: 600; }

h3 {
  font-size: 24px;
  line-height: 28px;
  font-weight: 600; }

ul {
  padding: 0;
  margin: 0; }
  ul li {
    list-style: none; }

img {
  width: 100%;
  max-width: 100%;
  height: auto; }

a {
  transition: all 0.3s;
  text-decoration: none;
  color: #FFFFFF; }
  a:hover {
    text-decoration: none; }

button {
  cursor: pointer;
  outline: none !important;
  border: none;
  background: transparent; }

svg,
