  transition: all 0.3s; }

input {
  outline: none;
  border: none;
  box-shadow: none; }
  input:focus {
    outline: none; }
  input::placeholder {
    color: #C4C4C4;
    font-size: 16px;
    line-height: 19px; }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

html,
body {
  max-width: 100%;
  overflow-x: hidden; }

body {
  font-size: 16px;
  font-family: "gilroyregular", sans-serif;
  color: #000;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400; }

.outer-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.container {
  margin: 0 auto; }

@media (min-width: 1200px) {
  .container {
    max-width: 1400px;
    width: 95%;
    padding-right: 15px;
    padding-left: 15px; } }

@media (max-width: 767px) {
  .container {
    padding-right: 20px;
    padding-left: 20px; } }

